import React from "react";
import PropTypes from "prop-types";
import moment from "moment";
import { withStyles } from "@material-ui/core/styles";
import cx from "classnames";

import configMap from "Utils/GetGlobals";

const styles = () => ({
  facebookItemContainer: {
    marginBottom: "30px"
  },
  imageWrapper: {
    marginBottom: "15px"
  },
  dateWrapper: {
      marginBottom: "5px"
  }
});

class FacebookItem extends React.Component {
  constructor(props) {
    super(props);

    moment.locale("de");
  }

  render() {
    const { classes, value } = this.props;
    return (
      <div className={cx(classes.facebookItemContainer, "col-xs-12 col-sm-6 col-md-4")}>
        <a
          href={value.permalink_url}
          title={configMap.openOnFacebookText}
          target="_blank"
          rel="noreferrer noopener"
        >
          {value.attachments &&
            value.attachments.data &&
            value.attachments.data[0].media && (
              <div className={cx(classes.imageWrapper, "image-wrapper")}>
                <img
                  src={value.attachments.data[0].media.image.src}
                  alt={value.attachments.data[0].title}
                />
              </div>
            )}
          <div className={cx(classes.dateWrapper, "date-wrapper")}>
            {new moment(value.created_time).format("Do MMMM YYYY")}
          </div>
          <div className="message-wrapper">{value.message}</div>
        </a>
      </div>
    );
  }
}

FacebookItem.propTypes = {
  classes: PropTypes.object.isRequired,
  value: PropTypes.object.isRequired
};

export default withStyles(styles)(FacebookItem);
