import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import cx from "classnames";

import configMap from "Utils/GetGlobals";
import { unveil } from "Utils/Token";

import Loading from "../components/Loading";
import FacebookItem from "../components/FacebookItem";

// eslint-disable-next-line no-unused-vars
import log from "Log";

const styles = () => ({
  facebookItemsContainer: {
    marginBottom: "30px"
  },
  loadNextButton: {
    marginTop: "15px"
  }
});

class Facebook extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      data: [],
      nextUrl: null,
      showLoader: true
    };

    this.handleResponse = this.handleResponse.bind(this);
    this.loadNext = this.loadNext.bind(this);
  }

  componentDidMount() {
    // eslint-disable-next-line no-undef
    FB.api(
      `/${configMap.pageId}/feed`,
      {
        access_token: unveil(configMap.facebookAuthToken),
        fields: [
          "attachments",
          "message",
          "created_time",
          "picture",
          "permalink_url"
        ],
        limit: configMap.limit
      },
      response => {
        this.handleResponse(response);
      }
    );
  }

  handleResponse(response) {
    log(response);
    if (response && !response.error) {
      this.setState({
        data: [...this.state.data, ...response.data],
        nextUrl: response.paging.next,
        showLoader: false
      });
    } else {
      this.setState({
        nextUrl: null,
        showLoader: false
      });
    }
  }

  loadNext() {
    this.setState(
      {
        showLoader: true
      },
      () => {
        // eslint-disable-next-line no-undef
        FB.api(this.state.nextUrl, response => {
          this.handleResponse(response);
        });
      }
    );
  }

  render() {
    const { classes } = this.props;

    return (
      <div className={classes.facebookItemsContainer}>
        <div className="row">
          {this.state.data &&
            this.state.data.map(item => {
              return <FacebookItem key={item.id} value={item} />;
            })}
        </div>

        {this.state.showLoader && <Loading />}

        {this.state.nextUrl && (
          <button
            className={cx(classes.loadNextButton, "btn center-block")}
            type="button"
            title={configMap.loadMoreText}
            onClick={this.loadNext}
          >
            {configMap.loadMoreText}
          </button>
        )}
      </div>
    );
  }
}

Facebook.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(Facebook);
