const rotN = function(text, map) {
  var r = "",
    i,
    j,
    c,
    len = map.length;
  for (i = 0; i < text.length; i++) {
    c = text.charAt(i);
    j = map.indexOf(c);
    if (j >= 0) {
      c = map.charAt((j + len / 2) % len);
    }
    r = r + c;
  }
  return r;
};

const rot47 = function(text) {
  text = text.replace("\\\\", "\\");
  var r = "";
  r = rotN(
    text,
    "!#$%&()*+,-./0123456789:;<=>?@ABCDEFGHIJKLMNOPQRSTUVWXYZ[]^_abcdefghijklmnopqrstuvwxyz{|}~"
  );
  return r;
};

